@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  font-family: 'Poppins', sans-serif;
  background-color: #272932;
}


/* background-color: #FDF500; */
.i{
display: flex;
height: 100vh;
}

.i-left{
flex: 1 1;
display: flex;
align-items: center;
justify-content: center;
color: whitesmoke;
}

.i-right{
    flex: 1 1;
    position: relative;
}

.i-left-wrapper{
    padding: 50px;
    height: 60%;
    /* background-color: indigo; */
    flex-direction: column;
    justify-content: space-between;
}

.i-intro{
     font-size: 30px;
     font-weight: 300;
}

.i-name{
font-size:  50px;
}

.i-title{
    height: 50px;
    overflow: hidden;
}
.i-title-wrapper{
    height: 100%;
    -webkit-animation: move 10s ease-in-out infinite alternate;
            animation: move 10s ease-in-out infinite alternate;
}
@-webkit-keyframes move {
    25%{
        -webkit-transform:translateY(-50px);
                transform:translateY(-50px);
    }
    50%{
        -webkit-transform:translateY(-100px);
                transform:translateY(-100px);
    }
    75%{
        -webkit-transform:translateY(-150px);
                transform:translateY(-150px);
    }
    100%{
        -webkit-transform: translateY(-200px);
                transform: translateY(-200px);
    }
}
@keyframes move {
    25%{
        -webkit-transform:translateY(-50px);
                transform:translateY(-50px);
    }
    50%{
        -webkit-transform:translateY(-100px);
                transform:translateY(-100px);
    }
    75%{
        -webkit-transform:translateY(-150px);
                transform:translateY(-150px);
    }
    100%{
        -webkit-transform: translateY(-200px);
                transform: translateY(-200px);
    }
}

.i-title-item{
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: #FDF500;
    /* background-color: indigo; */
    display: flex;
    align-items: center;
}

.i-desc{

}

.i-bg{
    -webkit-clip-path: polygon(37% 0, 100% 0, 100% 20%, 100% 80%, 100% 100%, 29% 100%, 0 50%, 24% 20%);
            clip-path: polygon(37% 0, 100% 0, 100% 20%, 100% 80%, 100% 100%, 29% 100%, 0 50%, 24% 20%);
    background-color: #FDF500;
    width: 100%;
    height: 100%;
    position:absolute;
    /* z-index: -1; */
    /* Zeby nie bylo kurwa scrola w doł oraz w bok */
    top: 0;
    right: 0;
}
.i-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    /* ????? beka mozesz zmienic z idex zmiast tego */
    position:absolute;

}
.i-scroll{
    position: absolute;
    bottom: 20px;
}

